@mixin padding-start($value) {
    -webkit-padding-start: $value; 
    -moz-padding-start: $value;
    padding-inline-start: $value;
}
.section-iconbox {
    .iconbox-item {
        display: flex;
        align-items: center;
        .icon {
            flex: 0 0 40px; 
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: rgba(var(--main-color-rgba), 0.1);
            padding: 10px;
        }
        .content { 
            @include padding-start(12px); 
            .heading {
                font-size: var(--body-font-size);
                margin-bottom: 2px;
            }
            .subtext {
                margin: 0;
                color: var(--text-miscellaneous);
            }
        }
    }
}